/* Contact.css */
.contact-container {
  background-color: #1a1a1a;
  color: #e0e0e0;
  padding: 60px 20px;
  font-family: 'Inter', sans-serif;
}

.contact-header {
  text-align: center;
  margin-bottom: 50px;
}

.contact-title {
  font-family: var(--font-main);
  font-size: 2.8rem;
  margin-bottom: 10px;
  font-weight: 400;
  color: #ffffff;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.contact-subtitle {
  font-size: 1.2rem;
  color: #a0a0a0;
  margin-bottom: 20px;
}

.title-divider {
  width: 100px;
  height: 2px;
  background-color: #b6a97f;
  margin: 0 auto;
}

.contact-content {
  max-width: 1200px;
  margin: 0 auto;
}

.contact-info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  margin-bottom: 60px;
}

.contact-info-item {
  display: flex;
  align-items: center;
  background-color: #252525;
  padding: 25px;
  border-left: 3px solid #b6a97f;
}

.contact-icon-wrapper {
  width: 50px;
  height: 50px;
  background-color: #333;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 20px;
  color: #b6a97f;
  font-size: 1.3rem;
}

.contact-text h3 {
  font-size: 1.2rem;
  margin-bottom: 5px;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.contact-text p {
  font-size: 1rem;
  color: #b0b0b0;
  margin: 0;
}

.map-section {
  margin-bottom: 60px;
}

.section-title {
  font-size: 1.8rem;
  margin-bottom: 15px;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 400;
}

.map-container {
  border: 1px solid #333;
}

.map-placeholder {
  width: 100%;
  min-height: 400px;
  border: 0;
  background: linear-gradient(135deg, rgba(182, 169, 127, 0.15), rgba(26, 26, 26, 0.9));
  color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
  font-family: 'Inter', sans-serif;
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.map-placeholder:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 24px rgba(0, 0, 0, 0.4);
}

.map-placeholder__title {
  font-size: 1.2rem;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

.map-placeholder__subtitle {
  font-size: 0.95rem;
  color: #d0d0d0;
}

.contact-action {
  text-align: center;
  padding: 40px;
  background-color: #252525;
  border-top: 1px solid #333;
  border-bottom: 1px solid #333;
}

.action-title {
  font-size: 1.8rem;
  margin-bottom: 10px;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 400;
}

.action-subtitle {
  font-size: 1.1rem;
  color: #a0a0a0;
  margin-bottom: 30px;
}

.action-button {
  background-color: #b6a97f;
  color: white;
  padding: 15px 40px;
  font-size: 1.1rem;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: background-color 0.3s;
}

.action-button:hover {
  background-color: #b6a97f;
}

@media (max-width: 768px) {
  .contact-title {
    font-size: 2rem;
  }
  
  .contact-info-grid {
    grid-template-columns: 1fr;
  }
  
  .contact-info-item {
    padding: 20px;
  }
}
